import React, { useState, useEffect } from 'react';
import { Paper, Typography, Button, MobileStepper, Grid, Box } from '@mui/material';
import { KeyboardArrowLeft, KeyboardArrowRight } from '@mui/icons-material';
import foto1 from './assets/foto1.jpg';
import foto2 from './assets/foto2.jpg';
import IconButton from '@mui/material/IconButton';
import InfoIcon from '@mui/icons-material/Info';
import ImageListItemBar from '@mui/material/ImageListItemBar';

const images = [
  {
    title: 'Imagen 1',
    description: 'Descripcin de la imagen 1',
    imgPath: foto1,
  },
  {
    title: 'Imagen 2',
    description: 'Descripcin de la imagen 2',
    imgPath: foto2,
  },
  // Agrega ms objetos para ms imgenes
];

const MyCarousel = () => {
  const [activeStep, setActiveStep] = useState(0);
  const [hovered, setHovered] = useState(false);

  const maxSteps = images.length;

  const handleNext = () => {
    setActiveStep((prevActiveStep) => (prevActiveStep + 1) % maxSteps);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => (prevActiveStep - 1 + maxSteps) % maxSteps);
  };

  const handleMouseEnter = () => {
    setHovered(true);
  };

  const handleMouseLeave = () => {
    setHovered(false);
  };

  useEffect(() => {
    let interval;

    if (!hovered) {
      interval = setInterval(handleNext, 3000);
    }

    return () => {
      clearInterval(interval);
    };
  }, [activeStep, hovered]);

  return (
    <div>
      <Box style={{ display: 'flex', textAlign: 'center'}}>
      < Typography variant='h2' component="h1">SKATEDC WITH THE WASHINGTON AREA ROADSKATERS</Typography>
      </Box>
      <Grid container spacing={2} style={{ padding: '20px', width: '100%', }}>
      {/* Logo y Leyenda */}
      <Grid item xs={1} style={{ display: 'flex', alignItems: 'center' }}> 
      
          <Button size="small" onClick={handleBack} disabled={hovered}>
            <KeyboardArrowLeft />
          </Button>
        
        
      
      </Grid>
      <Grid item xs={10} >
      <Paper
        square
        elevation={0}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        style={{ overflow: 'hidden', padding:"2rem", height:"40rem", width: '100%', textAlign: 'center',position: 'relative', }}
      >

        <img
          style={{  height:"100%" }}
          src={images[activeStep].imgPath}
          alt={images[activeStep].title}
        />
            <ImageListItemBar
             style={{ height:"3rem", width: '100%', }}
            title={images[activeStep].title}
            subtitle={images[activeStep].description}
            actionIcon={
              <IconButton
                sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
                aria-label={`info about ${images[activeStep].title}`}
              >
                <InfoIcon />
              </IconButton>
            }
          />
         
      </Paper>
      <Box style={{ display: 'flex', justifyContent: 'center'}}>
          <MobileStepper
           steps={maxSteps}
          position="static"
          activeStep={activeStep}
        />
        </Box>
      </Grid>
      <Grid item xs={1} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center'}} > 
  
        {
          <Button size="small" onClick={handleNext} disabled={hovered}>
            
            <KeyboardArrowRight />
          </Button>
        }
       </Grid>
      </Grid>
     
    </div>
  );
};

export default MyCarousel;