import { Box, Typography, Grid } from "@mui/material";
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import { CardActionArea } from '@mui/material';

export default function Map() {
  return (
    <Box style={{ padding: '20px', width: '100%', }}>
        <Grid container spacing={2}>
        
            <Grid item xs>
            
            <Card >
       <CardActionArea>
        <CardMedia
          component="iframe"
           height="300rem"
            title="Google Map"
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3105.2303746742004!2d-77.03340162555095!3d38.89584684679809!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x89b7b79783bf893d%3A0x203e2fe928efb537!2sFreedom%20Plaza%2C%20Washington%2C%20DC%2020004!5e0!3m2!1ses-419!2sus!4v1701897431045!5m2!1ses-419!2sus"
          frameBorder="0"
            allowFullScreen
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
          Meeting point: Fredoom Plaza
          </Typography>
          <Typography variant="body2" color="text.secondary">
          we meet at 11:00am Eastern Time
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
      
            </Grid>
        </Grid>
    </Box>
  );
}


