import { Box, Typography, Grid } from "@mui/material";

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';

export default function Upcoming() {
  return (
    <Box style={{ padding: '20px', width: '100%', }}>
        <Grid container spacing={2}>
        <Grid item xs>
          <Typography sx={{ mt: 4, mb: 2 }} variant="h4" component="div">
            UPCOMING EVENTS:
          </Typography>
          
            <List >
             
                <ListItem>
                  <ListItemText
                    primary="Are posted on SkateDC's meetup.com Calendar"
                    />
                </ListItem>
                <ListItem>
                  <ListItemText
                    primary="Many events are posted only on our very active Facebook group"
                    />
                </ListItem>
                <ListItem>
                  <ListItemText
                    primary="Skates depart 10 minutes after scheduled starting times (unless cancelled by inclement weather)"
                    />
                </ListItem>
              
            </List>
        
        </Grid>
        </Grid>
    </Box>
  );
}


